.slider{
    width: 480px;
    height: 640px;
    margin: auto;
    &__img{
        width: 480px;
        margin: auto;
        margin-top: 30px;
        
    }
    &__text{ 
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 140px;
        text-align: center;
        margin-top: 20px;
        &-title{
            
            
            @include heading-3();
            line-height: 30px;
        }
        &-description{
            @include medium();
            line-height: 24px;
            
    
        }
        &-price{
            
            @include heading-3();
            line-height: 30px;
        }
    }
}