.tab-item{
    display: flex;
    flex-direction: row;
    justify-content: center;
    border: solid 0.1px;
    border-color: $borderLight;
    width: 115px;
    height: 46px;
    background-color: white;
    border-radius: $mainRadis;
    
    &__kettle{
        margin: auto;
        border-radius: $mainRadis;
        background-color: $textLight;
        width: 30px;
        height: 30px;
        margin-right:0.5rem;

    } 

    &__img{

        margin: auto;
        padding-top: 7px;
    }

    &__text{
        @include linkButton();
        margin: auto;
    }
    

}
.tab-item:hover,.tab-item:active{
    background-color: $borderDark;
    border-color: $borderDark;
    .tab-item__text{
        color: $textLight;
    }
}
