.adress{
    display: flex;
    flex-direction: row;
    white-space: nowrap;
    height: 24px;
    &__link{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    &__text{
        @include linkButton();
        text-align: right;
        margin-left: 8px;
        color: $textLight;
    }
}
.adress:hover{
    border-bottom: solid 2px;
    border-bottom-color: $textLight;
}
