$mainWidth: 660px;
$mainHeight: 660px;

.box{
    overflow: hidden;
    width: $mainWidth;
    height: $mainHeight;
    position: relative;
   

    &__img{
        width: 726px;
        height: 726px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition-duration: 1s;
        transition-property: width,height;
        
        
    }
    &__img:hover{
        height: 660px;
        width: 660px;
        
    }
}

