@import './utils';
@import './components';
@import './layout';

body{
    background-color: $bodyColor;
}
.container{
    width: 1440px;
    margin: auto;
    
}
.wrapper{
    width: 1360px;
    margin: auto;
}
.flex-row{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
}

.heading-1{
    @include heading-1();
}
.heading-2{
    @include heading-2();
}
.heading-3{
    @include heading-3();
}
.caption{
    @include caption();
}
.medium{
    @include medium();
}
.linkButton{
    @include linkButton();
}
.burger-link{
    @include burger-link();
}
.accent{
    @include accent();
}
.logo{
    width: 100px;
    height: 60px;
}
.app-img{
    width: 36px;
    height: 36px;
}
.coffeecup{
    width: 20px;
    height: 20px;
}
.primary-img{
    width: 20px;
    height: 20px;
}
.link-img{
    width: 20px;
    height: 20px;
}
.arrow-img{
    width: 24px;
    height: 24px;
}
.icon-img{
    width: 24px;
    height: 24px;
}
.content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 2rem;
}
.flex-column{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
