.card{
    width: 310px;
    height: 506px;
    display: flex;
    flex-direction: column;
    border: solid 1px;
    border-radius: 40px;
    border-color: $borderLight;
    background-color: white;
    &__wrapper{
        width: 308px;
        height: 310px;
        margin:auto;
        overflow: hidden;
        border-radius: 40px;
    }
    &__img{
        width: 100%;
        height: 100%;
        
        transition: all 1s ease-out;
        transform: scale(1.1);
    }
    &__img:hover{
        transform:none;
    }
    &__text{
        width: 270px;
        height: 196px;
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        &-title{
            margin-top: 20px;
            @include heading-3();
            line-height: 30px;
        }
        &-description{
            @include medium();
            line-height: 24px;
    
        }
        &-price{
            margin-bottom: 20px;
            @include heading-3();
            line-height: 30px;
        }
    }
    
    
}