.header-nav{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top:18px;
    
    &__text{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;    
        justify-content: center;
        @include medium();
        margin: auto;
        width: 100%;
        height: 28px;
        margin-top: 16px;
        &-item{
            @include linkButton();
            height: 28px;
            margin-right: 56px;
            scroll-behavior: smooth;
        }
        &-item:hover{
            border-bottom: solid 1px;
            border-bottom-color: $textDark;
        }
    }
    
    &__link{
        
        width: 82.5px;
        height: 28px;
        margin: auto;
        margin-right: 0;
    }
}