.buttons{
    &-primary{
        @include buttons;
        
        &__text{
            color: $textDark;
            margin: auto;
            @include linkButton;
        }
        &__img{
            display: none;
            margin:  auto;
            margin-left: 0;
        }
        
    }
    &-secondary{
        @include buttons;
        height: 44px;
        background-color: white;
        
        &__text{
            @include buttonsText
        }
    }
    &-app{
        @include buttons;
        
        background-color: white;
        
        &__text{
            @include buttonsText;
            text-align: left;
            width: 90px;
            height: 40px;
            margin-bottom: 1.2rem;
            margin-right: 38.5px;
            

            
            
            // margin-bottom: 0.5rem;
        }
        &__img{
            display: block;
            margin-left: 15px;
            margin-top: 0.6rem;
        }
        &__img-hover{
            display: none;
            margin-left: 15px;
            margin-top: 0.6rem;

        }
    }
    &-arrow{
        @include buttons;
        width: 60px;
        height: 60px;
        background-color: white;
        &__img{
            display: block;
            margin: auto;
        }
        &__img-hover{
            display: none;
            margin: auto;
        }
    }
    &-instagram{
        @include buttons;
        width: 60px;
        height: 60px;
        background-color: inherit;

        &__img{
            margin: auto;
            
        }
        &__img-hover{
            
            // background-blend-mode: overlay;
            color: $borderLight;
            margin: auto;
        }
    }
    &-menu{
        @include buttons;
        width: 60px;
        height: 60px;
        background-color: white;
        &__img{
            display: block;
            margin: auto;
        }
        &__img-hover{
            display: none;
            margin: auto;
        }
    }
    
}
.buttons-primary:hover{
    .buttons-primary__img{
        display: block;
    }
    .buttons-primary__text{
        margin-right: 1rem;
    }
}
.buttons-secondary:hover{
    background-color: $borderDark;
    border-color: $borderDark;
    .buttons-secondary__text{
        color: $textLight;
    }
}
.buttons-app:hover{
    background-color: $borderDark;
    border-color: $borderDark;
    .buttons-app__text{
        color: $textLight;
    }
    .buttons-app__img{
        display:none;
    }
    .buttons-app__img-hover{
        display:block;
    }
}
.buttons-arrow:hover{
    background-color: $borderDark;
    border-color: $borderDark;
    .buttons-arrow__img{
        display:none;
    }
    .buttons-arrow__img-hover{
        display:block;
    }
}
.buttons-instagram:hover{
    background-color: $borderLight;
    border-color: $borderLight;
    
}
.buttons-menu:active{
    .buttons-menu__img{
        display:none;
    }
    .buttons-menu__img-hover{
        display:block;
    }
}