.enjoy{
    @include container();
    overflow: hidden;
    margin-top: 20px;
    
    &-background{
        @include container();
        width: 100%;
        height: 644px;
        border-radius: 40px;
        
        
    
    }
    &__list{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 530px;
        height: 544px;
        
        margin: 0 0 100px 100px;
        
        
        &-title{
            margin-top: 100px;
            @include heading-1();
            color: $textLight;
            &__accent{
                color: $textAccent;
                font-style: oblique 10deg;
                line-height: 75.6px;
            }
        }
        &-description{
            @include medium();
            color: $textLight;
        }
    }
}