//Heading

@mixin container{
    width: 1440px;
    margin: auto;
}
@mixin heading-1{
    font-size: 42px;
    font-weight: 600;
    line-height: 115%;
    color: $textDark;
    font-family: $mainFont;

    @media ($tablet) {
        font-size: 72px;
        line-height: 105%;
        
    }
   
}


@mixin heading-2{
    font-size: 32px;
    font-weight: 600;
    line-height: 125%;
    color: $textDark;
    font-family: $mainFont;
    @media ($tablet) {
        font-size: 60px;
        line-height: 125%;
    }
    
   
}

@mixin heading-3{
    font-size: 24px;
    font-weight: 600;
    line-height: 125%;
    color: $textDark;
    font-family: $mainFont;
}


//body
@mixin medium{
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    color: $textDark;
    font-family: $mainFont;
}

@mixin caption{
    font-size: 10px;
    font-weight: 600;
    line-height: 140%;
    color: $textDark;
    font-family: $mainFont;

}
//Action
@mixin linkButton{
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
    color: $textDark;
    font-family: $mainFont;
}
@mixin burger-link{
    font-size: 32px;
    font-weight: 600;
    line-height: 125%;
    color: $textDark;
    font-family: $mainFont;
}

@mixin accent{
    font-style: italic;
}
@mixin buttons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 200px;
    height: 64px;
    background-color: $borderLight;
    border: solid 1px;
    border-radius: $mainRadis;
    border-color: $borderLight;
        
}
@mixin buttonsText{
    margin: auto;
    color: $textDark;
    @include linkButton;
}
